html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Palatino Linotype";
  font-style: normal;
  font-weight: 400;
  src: local("Palatino Linotype"), local("PalatinoLinotype-Roman"), url("../fonts/PalatinoLinotype-Roman.woff2") format("woff2"), url("../fonts/PalatinoLinotype-Roman.woff") format("woff");
}

@font-face {
  font-family: "Palatino Linotype";
  font-style: italic;
  font-weight: 400;
  src: local("Palatino Linotype Italic"), local("PalatinoLinotype-Italic"), url("../fonts/PalatinoLinotype-Italic.woff2") format("woff2"), url("../fonts/PalatinoLinotype-Italic.woff") format("woff");
}

@font-face {
  font-family: "Palatino Linotype";
  font-style: normal;
  font-weight: 700;
  src: local("Palatino Linotype Bold"), local("PalatinoLinotype-Bold"), url("../fonts/PalatinoLinotype-Bold.woff2") format("woff2"), url("../fonts/PalatinoLinotype-Bold.woff") format("woff");
}

@font-face {
  font-family: "Palatino Linotype";
  font-style: italic;
  font-weight: 700;
  src: local("Palatino Linotype Bold Italic"), local("PalatinoLinotype-BoldItalic"), url("../fonts/PalatinoLinotype-BoldItalic.woff2") format("woff2"), url("../fonts/PalatinoLinotype-BoldItalic.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  color: #121014;
  background: #f0f0f0;
  font-family: "Palatino Linotype";
  line-height: 1.4;
}

.inner {
  position: relative;
  padding-left: 8px;
  padding-right: 8px;
  margin: 0 auto;
  max-width: 642px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 4px 15px 6px;
  border: 1px solid #bcbcbc;
  background: #e1e1e1;
  font-family: inherit;
  font-size: 12px;
  line-height: 1;
  transition: border-color 0.25s, background 0.25s;
}

.btn:hover, .btn:active {
  border-color: #0979d7;
}

.btn:active {
  background: #d4d4d4;
}

.header {
  padding: 7px 0;
  color: #fff;
  background: #0177d7;
}

.header__title {
  font-size: 12px;
  font-weight: normal;
}

.hero {
  padding: 23px 0 3px;
  border-bottom: 1px solid #a9b1b6;
  color: #000;
  background: #87cfeb;
}

.hero__title {
  padding: 0 0 12px 13px;
  font-size: 36px;
}

.hero__description {
  font-size: 18px;
  text-align: right;
}

.content {
  padding: 10px 0 30px;
  font-size: 14px;
}

.content__inner {
  max-width: 570px;
}

.content__title {
  font-size: 16px;
}

.content__title:not(:last-child),
.content p:not(:last-child) {
  margin-bottom: 1em;
}

.footer {
  padding: 20px 5px;
  border-top: 1px solid #b6b6b6;
}

.footer__buttons {
  text-align: right;
}

.footer__btn {
  margin-left: 15px;
}
