//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight) {
	font-family: $familyPrimary;
	font-style: $style;
	font-weight: $weight; }

@font-face {
	@include fontdefault(normal, 400);
	@include fontpath('Palatino Linotype', 'PalatinoLinotype-Roman', PalatinoLinotype-Roman); }
@font-face {
	@include fontdefault(italic, 400);
	@include fontpath('Palatino Linotype Italic', 'PalatinoLinotype-Italic', PalatinoLinotype-Italic); }
@font-face {
	@include fontdefault(normal, 700);
	@include fontpath('Palatino Linotype Bold', 'PalatinoLinotype-Bold', PalatinoLinotype-Bold); }
@font-face {
	@include fontdefault(italic, 700);
	@include fontpath('Palatino Linotype Bold Italic', 'PalatinoLinotype-BoldItalic', PalatinoLinotype-BoldItalic); }
